import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //
    artImage: [],
    //
    otherImage: [],
    //
    isFirst: true,
    //
    hightPlay: false,
    //
    lowPlay: false,
    //
    mainPlay: false,
    //
    mainControl: true,
    hightControl: false,
    lowControl: false
  },
  mutations: {
      setState(state, data){
         state[data.name] = data.value;
     }
  }
})
